import Vue from 'vue'
import Router from 'vue-router'
import home from "@/components/pages/home.vue"
import about from "@/components/pages/about.vue"
import courses from "@/components/pages/courses.vue"
import placement from "@/components/pages/placement.vue"
import news from "@/components/pages/news.vue"
import contact from "@/components/pages/contact.vue"
import details from "@/components/pages/details.vue"
import details1 from "@/components/pages/details1.vue"
import details2 from "@/components/pages/details2.vue"
import details3 from "@/components/pages/details3.vue"
import details4 from "@/components/pages/details4.vue"
import details5 from "@/components/pages/details5.vue"
import details6 from "@/components/pages/details6.vue"
import fournotfour from "@/components/pages/fournotfour.vue"
Vue.use(Router)
export default new Router({
    mode:"history",
    routes:[
        {
            path: '/',
            name:'home',
            component:home
        },
        {
            path:"/about",
            name:'about',
            component:about
        },
        {
            path:"/courses",
            name:'courses',
            component:courses
        },
        {
            path:"/placement",
            name:'placement',
            component:placement
        },
        {
            path:"/news",
            name:'news',
            component:news
        },
        {
            path:"/contact",
            name:'contact',
            component:contact
        },
        {
            path:"/details",
            name:'details',
            component:details
        },
        {
            path:"/details1",
            name:'details1',
            component:details1
        },
        {
            path:"/details2",
            name:'details2',
            component:details2
        },
        {
            path:"/details3",
            name:'details3',
            component:details3
        },
            {
                path:"/details4",
                name:'details4',
                component:details4
            },
            {
                path:"/details5",
                name:'details5',
                component:details5
            },
            {
                path:"/details6",
                name:'details6',
                component:details6
            },
        {
            path:"*",
            name:'404',
            component:fournotfour
        }
    ]
})